/**
 * Write-only the password as cookie
 */
import React, { useState } from 'react';
import { setSessionPassword } from '../utils/utils';

const PasswordProtect = () => {
  const [password, setPassword] = useState('');

  const onSubmit = event => {
    event.preventDefault();
    setSessionPassword(password);
    // var origin = location.origin;
    // window.location.replace(origin);
    // window.location.href="/";
    
    window.location.reload(true); // eslint-disable-line
  };

  return (
    <div className="container-fluid px-0">
      <div key="password-page" style={{
    height: `100vh`,
    display: `flex`,
    flexDirection: `column`,
    justifyContent: `center`
  }}>
        <h1 style={{ textAlign:`center`, marginBlockStart: `0`, marginBlockEnd: `.75rem`, lineHeight: `2.5rem`, fontSize: `1.5rem`}}>
          Eunji Jinny Seo • UX Portfolio
        </h1>

        <h2 style={{ textAlign:`center`, marginBlockStart: `0`, marginBlockEnd: `0`, fontWeight:`300`, color:`#555` }}>Please enter password to view</h2>


        <form onSubmit={onSubmit} className="d-sm-flex justify-content-center" style={{
    padding: `3rem`,
    margin:`.5rem`,
    
  }}>
          <input
            autofocus="true"
            className="form-control form-control-lg"
            name="password"
            type="password"
            value={password}
            onChange={event => setPassword(event.target.value)}
            style={{
              fontFamily: `"Karla",sans-serif`,
              letterSpacing: `.025rem`,
              margin:`1rem .25rem`,
              minWidth: `120px`,
              maxWidth: `320px`
            }}
          />

          <button className="btn btn-lg btn-primary btn-responsive" style={{
    fontFamily: `"Karla",sans-serif`,
    letterSpacing: `.025rem`,
    margin:`1rem .25rem`,
    minWidth: `120px`,
    maxWidth: `320px`
  }}
            type="submit"
          >

            Enter
          </button>
        </form>
        </div>

    </div>
  );
};

export default PasswordProtect;
